let currentPageName = "auto-create-dialog";

export default {
  name: currentPageName,
  data() {
    return {
      autoCreateDialogDataRule: {
        dayListen: [
          { required: true, message: '此参数不能为空' },
          {
            type: 'number',
            message: '日听课次数必须为数字值'
          }
        ],
        maxPeople: [
          { required: true, message: '此参数不能为空' },
          {
            type: 'number',
            message: '督导人数必须为数字值'
          }
        ],
        minPeople: [
          { required: true, message: '此参数不能为空' },

          {
            type: 'number',
            message: '督导人数必须为数字值'
          }
        ],
        weekListen: [
          { required: true, message: '此参数不能为空' },

          {
            type: 'number',
            message: '周听课次数必须为数字值'
          }
        ],
        total: [
          { required: true, message: '此参数不能为空' },

          {
            type: 'number',
            message: '总听课安排必须为数字值'
          }
        ],
        apercent: [
          { required: true, message: '此参数不能为空' },
          {
            type: 'number',
            message: '日理论课占比例必须为数字值'
          }
        ],

        endWeek: [
          { required: true, message: '此参数不能为空' },
        ],
        startDay: [
          { required: true, message: '此参数不能为空' },
        ],
        startWeek: [
          { required: true, message: '此参数不能为空' },
        ],
      },
      autoCreateDialogData: {
        apercent: 0,
        dayListen: '',
        endWeek: '',
        maxPeople: '',
        minPeople: '',
        startDay: '',
        startWeek: '',
        total: '',
        weekListen: '',
      },
      selectDayList: [
        {
          name: '星期一',
          value: 1
        }, {
          name: '星期二',
          value: 2
        }, {
          name: '星期三',
          value: 3
        }, {
          name: '星期四',
          value: 4
        }, {
          name: '星期五',
          value: 5
        }, {
          name: '星期六',
          value: 6
        }, {
          name: '星期天',
          value: 7
        }
      ],
      constValue: false
    }
  },
  props: {
    showAutoCreateDialog: {
      type: Boolean,
      default: false
    },
    autoCreateMode: {
      type: String,
      default: 'create'
    },
    btnCancel: {
      type: Function,
      default: function () {
      }
    },
    btnOk: {
      type: Function,
      default: function () {
      }
    },
    isRestAll: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    getBtnOkDisabled: function () {
      let { apercent,
        dayListen,
        endWeek,
        maxPeople,
        minPeople,
        startDay,
        startWeek,
        total,
        weekListen } = this.autoCreateDialogData
      return !(apercent > 0 &&
        dayListen > 0 &&
        endWeek > 0 &&
        maxPeople > 0 &&
        minPeople > 0 &&
        startDay > 0 &&
        startWeek > 0 &&
        weekListen > 0 && total > 0)
    }

  },
  watch: {
    isRestAll: function (value) {
      // alert(value)
      let that = this
      if (value !== that.constValue) {
        this.resetAll()
        that.constValue = value
      }
    }


  },
  methods: {
    resetAll: function () {
      let that = this
      that.autoCreateDialogData = {
        apercent: 0,
        dayListen: '',
        endWeek: '',
        maxPeople: '',
        minPeople: '',
        startDay: '',
        startWeek: '',
        total: '',
        weekListen: ''
      }
      that.$refs.autoCreateDialog.resetFields()
    }
  }

}




